export default {
	data() {
		return {
			list:[],
			index:-1,
			type:'checkbox',
			column:''
		};
	},
	methods: {
		/* 切换 */
		change(e){
			let i = Number(e.currentTarget.dataset.i);
			/* 单选框 */
			if(this.type=='radio'){
				this.index = i;
				this.$nextTick(()=>{
					this.$emit("change",this.get());
				})
				return;
			}
			/* 复选框 */
			if(this.list[i].checked){
				this.$set(this.list[i],"checked",false);
			}else{
				this.$set(this.list[i],"checked",true);
			}
			this.$nextTick(()=>{
				this.$emit("change",this.get());
			})
		},
		/* 设置值 */
		set(data) {
			let [type,index] = ['checkbox',-1];
			let column = ['','col_1','col_2','col_3']
			if(data.type == 'radio'){
				type = 'radio';
				index = data.index >= 0 ? data.index : -1;
			}
			this.column = (data.column in column) ? column[data.column] : '';
			this.type = type;
			this.index = index;
			this.list = data.list;
		},
		/* 获取值 */
		get(){
			/* 单选框 */
			if(this.type=='radio'){
				if(this.index >= 0){
					return this.list[this.index];
				}else{
					return null;
				}
			}
			
			let arr=[];
			this.list.forEach((item,index)=>{
				if(item.checked == true){
					arr.push(item);
				}
			});
			return arr.length > 0 ? arr : null;
		},
		/* 全部选中 */
		checkAll(){
			if(this.type=='radio'){
				return null;
			}
			this.list.forEach((item,index)=>{
				this.$set(this.list[index],"checked",true);
			})
		},
		/* 取消全部选中 */
		cancelAll(){
			if(this.type=='radio'){
				this.index = -1;
				return null;
			}
			this.list.forEach((item,index)=>{
				this.$set(this.list[index],"checked",false);
			})
		},
		/* 反选全部 */
		invertAll(){
			if(this.type=='radio'){
				this.index = -1;
				return null;
			}
			this.list.forEach((item,index)=>{
				this.$set(this.list[index],"checked",item.checked ? false : true);
			})
		}
	}
}
